body {
  box-sizing: content-box !important; }

*,
*:before,
*:after {
  box-sizing: inherit !important; }

.f-fieldset {
  position: relative;
  font-size: inherit;
  text-align: left; }
  .f-fieldset *,
  .f-fieldset *:before,
  .f-fieldset *:after {
    box-sizing: border-box; }
  .f-fieldset__label {
    user-select: none;
    font-size: 1em; }
  .f-fieldset__label-text {
    color: gray;
    color: inherit;
    font-size: 1em; }
    .f-fieldset__field[disabled].f-fieldset__field[disabled] + .f-fieldset__label .f-fieldset__label-text {
      color: silver; }
  .f-fieldset__indicator {
    pointer-events: none;
    user-select: none;
    color: silver; }
    .f-fieldset__indicator--optional {
      font-size: 0.5em; }
    .f-fieldset__indicator--required {
      font-size: 1em; }
    .has-error .f-fieldset__field:hover ~ .f-fieldset__indicator,
    .has-error .f-fieldset__field:focus ~ .f-fieldset__indicator {
      color: #e91e63 !important; }
    .f-fieldset__field[disabled].f-fieldset__field[disabled] ~ .f-fieldset__indicator {
      color: silver; }
  .f-fieldset__message {
    font-size: 0.8em; }
    .f-fieldset__message--instruction {
      color: gray; }
    .f-fieldset__message--error {
      color: #e91e63; }
      .has-error .f-fieldset__message--error::-moz-selection {
        color: white;
        background: #e91e63 !important;
        text-shadow: none; }
      .has-error .f-fieldset__message--error::selection {
        color: white !important;
        background: #e91e63 !important;
        text-shadow: none; }
    .f-fieldset__message--error + .f-fieldset__message--instruction {
      display: none; }
  .f-fieldset {
    border: 0;
    margin: 0;
    padding: 0; }
  .f-fieldset__legend {
    position: relative;
    width: 100%;
    padding: 0; }
  .f-fieldset__inner--stacked {
    display: flex;
    flex-wrap: wrap; }
    .f-fieldset__inner--stacked > * {
      width: 100%; }
      .f-fieldset__inner--stacked > *:not(:last-child) {
        margin-bottom: 1.25em; }
  .f-fieldset__inner--one-line {
    display: flex;
    flex-wrap: wrap; }
    .f-fieldset__inner--one-line > * {
      width: auto; }
      .f-fieldset__inner--one-line > *:not(:last-child) {
        margin-right: 1.25em; }
